        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-rectangle</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"rectangle1"</span> <span class="hljs-attr">:coordinates</span>=<span class="hljs-string">"coordinates1"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-rectangle</span>&gt;</span>
