<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">rotation</span>: <span class="hljs-number">0</span>,
        <span class="hljs-attr">rectangle1</span>: {},
        <span class="hljs-attr">coordinates1</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">-110</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">20</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">-80</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">25</span> },
        <span class="hljs-attr">material1</span>: {},

        <span class="hljs-attr">rectangle2</span>: {},
        <span class="hljs-attr">coordinates2</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">-110</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">30</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">-100</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">40</span> },
        <span class="hljs-attr">material2</span>: {},
        <span class="hljs-attr">rotation2</span>: {},
        <span class="hljs-attr">outlineColor2</span>: {},

        <span class="hljs-attr">rectangle3</span>: {},
        <span class="hljs-attr">coordinates3</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">-92.0</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">30.0</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">-82.0</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">40.0</span> },
        <span class="hljs-attr">material3</span>: {},
        <span class="hljs-attr">rotation3</span>: {},
        <span class="hljs-attr">stRotation3</span>: {},
        <span class="hljs-attr">classificationType3</span>: <span class="hljs-number">0</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([
        <span class="hljs-keyword">this</span>.$refs.rectangle1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.rectangle2.createPromise,
        <span class="hljs-keyword">this</span>.$refs.rectangle3.createPromise
      ]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.rotation = Cesium.Math.toRadians(<span class="hljs-number">30</span>)
        <span class="hljs-keyword">this</span>.material1 = Cesium.Color.RED.withAlpha(<span class="hljs-number">0.5</span>)

        <span class="hljs-keyword">this</span>.material2 = Cesium.Color.GREEN.withAlpha(<span class="hljs-number">0.5</span>)
        <span class="hljs-keyword">this</span>.rotation2 = Cesium.Math.toRadians(<span class="hljs-number">45</span>)
        <span class="hljs-keyword">this</span>.outlineColor2 = Cesium.Color.BLACK

        <span class="hljs-keyword">this</span>.material3 = <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>
        <span class="hljs-keyword">this</span>.outlineColor3 = <span class="hljs-string">`BLACK`</span>
        <span class="hljs-keyword">this</span>.rotation3 = <span class="hljs-keyword">new</span> Cesium.CallbackProperty(<span class="hljs-keyword">this</span>.getRotationValue, <span class="hljs-literal">false</span>)
        <span class="hljs-keyword">this</span>.stRotation3 = <span class="hljs-keyword">new</span> Cesium.CallbackProperty(<span class="hljs-keyword">this</span>.getRotationValue, <span class="hljs-literal">false</span>)
        <span class="hljs-keyword">this</span>.classificationType3 = Cesium.ClassificationType.TERRAIN
      },
      getRotationValue() {
        <span class="hljs-keyword">this</span>.rotation += <span class="hljs-number">0.005</span>
