<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 rectangle 是否显示。</td>
</tr>
<tr>
<td>coordinates</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 rectangle 的 Rectangle 属性。<strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 rectangle 高度。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 rectangle 高度模式。<strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 rectangle 拉伸高度。</td>
</tr>
<tr>
<td>extrudedHeightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 rectangle 拉伸高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 rectangle 按正北顺时针的旋转角。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 rectangle 按正北逆时针旋转纹理。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 rectangle 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'WHITE'</code></td>
<td><code v-pre>optional</code> 指定 rectangle 材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 rectangle 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'BALCK'</code></td>
<td><code v-pre>optional</code> 指定 rectangle 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 rectangle 轮廓线宽度。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 rectangle 是否投射或接收阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 rectangle 随相机距离改变是否显示参数。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定 rectangle 贴对象模式 。 <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2, NUMBER_OF_CLASSIFICATION_TYPES: 3</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 rectangle 顺序，没设置高度和拉伸高度时有效。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/RectangleGraphics.html">RectangleGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          rotation: 0,
          rectangle1: {},
          coordinates1: { west: -110, south: 20, east: -80, north: 25 },
          material1: {},

          rectangle2: {},
          coordinates2: { west: -110, south: 30, east: -100, north: 40 },
          material2: {},
          rotation2: {},
          outlineColor2: {},

          rectangle3: {},
          coordinates3: { west: -92.0, south: 30.0, east: -82.0, north: 40.0 },
          material3: {},
          rotation3: {},
          stRotation3: {},
          classificationType3: 0
        }
      },
      mounted() {
        Promise.all([
          this.$refs.rectangle1.createPromise,
          this.$refs.rectangle2.createPromise,
          this.$refs.rectangle3.createPromise
        ]).then((instances) => {
          instances[0].viewer.zoomTo(instances[0].viewer.entities)
        })
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.rotation = Cesium.Math.toRadians(30)
          this.material1 = Cesium.Color.RED.withAlpha(0.5)

          this.material2 = Cesium.Color.GREEN.withAlpha(0.5)
          this.rotation2 = Cesium.Math.toRadians(45)
          this.outlineColor2 = Cesium.Color.BLACK

          this.material3 = 'https://zouyaoji.top/vue-cesium/favicon.png'
          this.outlineColor3 = `BLACK`
          this.rotation3 = new Cesium.CallbackProperty(this.getRotationValue, false)
          this.stRotation3 = new Cesium.CallbackProperty(this.getRotationValue, false)
          this.classificationType3 = Cesium.ClassificationType.TERRAIN
        },
        getRotationValue() {
          this.rotation += 0.005
          return this.rotation
        }
      }
    }
  </script>